/*EB Garamond + Barlow */
@import url('https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=EB+Garamond:ital,wght@0,400..800;1,400..800&display=swap');

/* Fonts */
$font-serif: "EB Garamond", serif;
$font-sans: "Barlow", sans-serif;

/* Sizes */
$font-size-large: 3.5rem;
$font-size-medium: 1rem;
$font-size-small: .68rem;

/* Colors */
$color-red: rgba(202, 25, 1, 0.946);
$color-orange: rgba(227, 78, 8, 0.946);
$color-white: rgb(238, 241, 232);
$color-bg: #f76c26;
$color-fg: #2f0449cb;
$color-line: rgba(35, 35, 34, 0.614);
$color-soft-line: rgba(35, 35, 34, 0.306);
$color-body-bg: #dcd8c9;
$color-dp-bg: rgb(238, 234, 221);  
$color-text: #535055cd;
$color-strong: #2a282bd4;
$color-link: #3a393bf7;
$color-title:rgba(29, 27, 30, 0.912);
$color-light: #24242474;
$color-lighter: #7373733b;

$padding: 1.52rem;
$page-gradient: linear-gradient(90deg, rgba(238,234,221,1) 48%, rgba(223,219,207,1) 50%, rgba(238,234,221,1) 52%);